import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '@/components/common/upload-images.vue';
export default {
	components:{
		uniNavBar,
		uploadImages
	},
	data() {
		return {
			content:'',
			imageList:[],
			showBack:false
		}
	},
	computed:{
		show(){
			return this.imageList.length>0
		}
		
	},
	onLoad() {
		uni.getStorage({
			key:'add-input',
			success:(res)=>{
				let result = JSON.parse(res.data)
				this.content = result.content
				this.imageList = result.imageList
				console.log(this.imageList);
			}
		})
	},
	methods: {
		iconClickEvent(e){
			switch(e){
				case 'uploadImage':
					this.$refs.uploadImage.chooseImage()
				break;
			}
		
		},
		store(){
			uni.setStorage({
				key:'add-input',
				data:JSON.stringify({
					content:this.content,
					imageList:this.imageList,
				}),
			  success:function() {
					
				}
			})
		},
		changeImages(e){
			this.imageList = e;
		},
		back() {
			if((this.content !== '' || this.imageList.length>0 ) && !this.showBack){
				uni.showModal({
					content: '是否要保存草稿',
					showCancel: true,
					cancelText: '不保存',
					confirmText: '保存',
					success: res => {
						if(res.confirm){
							console.log('保存')
							this.store();
						}else{
						uni.removeStorage({
							key:'add-input'
						});
						}
						uni.navigateBack({delta: 1});
					}
				});
				this.showBack = true;
				return true
			}
			uni.navigateBack({delta: 1});
		}
		
	}
}
